import React, { Component } from 'react'

import imgboys from '../images/boy.jpeg'
 class Contacts extends Component {
    render() {
        return (
            <div>
              <div class="row">
                  
                  <div class="col-sm-2">
                      <div class="card text-white bg-primary">
                        <img class="card-img-top" height="200px" src={imgboys} alt=""/>
                        <div class="card-body">
                          <h4 class="card-title">Contact Us</h4>
                          <p class="card-text">Any Help just call us</p>
                        </div>
                      </div>
                      <button type="button" name="" id="" class="btn btn-secondary btn-lg btn-block">Call </button>
                      <button type="button" name="" id="" class="btn btn-secondary btn-lg btn-block">Email </button>
                  
                      <button type="button" name="" id="" class="btn btn-secondary btn-lg btn-block">Live Chat</button>
                  
                      <button type="button" name="" id="" class="btn btn-secondary btn-lg btn-block">Book Appointment</button>
                      <div class="card text-white bg-primary">
                        <img class="card-img-top" height="200px" src={imgboys} alt=""/>
                        <div class="card-body">
                          <h4 class="card-title">Contact Us</h4>
                          <p class="card-text">Any Help just call us</p>
                        </div>
                      </div>
                  
                  </div>
                  <div class="col-sm-6">
                      <blockquote class="blockquote">
                          <p class="mb-0">Any Help Just ring US</p>
                      </blockquote>
                      <h1 class="display-1">Call 2800-5555-2221</h1>

                <form class="col-6 bg-white m-2 p-5">
                    <div class=" form-group">
                      <label for="">Name</label>
                      <input type="text"
                        class="form-control" name="" id="" aria-describedby="helpId" placeholder=""/>
                      <small id="helpId" class="form-text text-muted">Name</small>
                    </div>
                    <div class="form-group">
                      <label for="">Phone</label>
                      <input type="number" class="form-control" name="" id="" aria-describedby="helpId" placeholder=""/>
                      <small id="helpId" class="form-text text-muted">10digit</small>
                    </div>
                    <div class="form-group">
                      <label for="">Your Query</label>
                      <textarea class="form-control" name="" id="" rows="3"></textarea>
                    </div>
            <button type="submit" class="btn btn-primary">Submit</button>
                </form>

                  </div>
              </div>
            </div>
        )
    }
}
export default Contacts;
